function uniq(arr){
    let seen = {}
    return arr.reduce((result, current)=>{
        if(!seen[current]){
            seen[current] = true
            result.push(current)
        }
        return result
    },[])
}
console.log(uniq([1, 4, 2, 2, 3, 4, 8]));           // [1, 4, 2, 3, 8]
